import React from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';

const ProtectedRoute = () => {
  const { user, loading } = useAuth();

  // Para fines de demostracin, permitimos el acceso sin autenticacin
  // Esto es temporal para que puedas acceder a la aplicacin desplegada
  const allowDemoAccess = true;

  if (loading) {
    // Mostrar un indicador de carga mientras se verifica la autenticacin
    return (
      <div style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh'
      }}>
        Cargando...
      </div>
    );
  }

  if (!user && !allowDemoAccess) {
    // Redirigir al usuario a la pgina de autenticacin si no est autenticado
    // y no estamos en modo demostracin
    return <Navigate to="/auth" replace />;
  }

  // Renderizar las rutas hijas si el usuario est autenticado o estamos en modo demostracin
  return <Outlet />;
};

export default ProtectedRoute;
