import React from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

// Configuracin global para las notificaciones
const toastConfig = {
  position: "top-right",
  autoClose: 3000,
  hideProgressBar: false,
  closeOnClick: true,
  pauseOnHover: true,
  draggable: true,
  progress: undefined,
};

// Funcin para mostrar una notificacin de xito
export const showSuccess = (message) => {
  toast.success(message, toastConfig);
};

// Funcin para mostrar una notificacin de error
export const showError = (message) => {
  toast.error(message, toastConfig);
};

// Funcin para mostrar una notificacin de informacin
export const showInfo = (message) => {
  toast.info(message, toastConfig);
};

// Funcin para mostrar una notificacin de advertencia
export const showWarning = (message) => {
  toast.warning(message, toastConfig);
};

// Componente contenedor para las notificaciones
const Notification = () => {
  return (
    <ToastContainer
      position="top-right"
      autoClose={3000}
      hideProgressBar={false}
      newestOnTop
      closeOnClick
      rtl={false}
      pauseOnFocusLoss
      draggable
      pauseOnHover
    />
  );
};

export default Notification;
