import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAccessibility } from '../../contexts/AccessibilityContext';

/**
 * Componente para habilitar la navegacin por teclado
 * @returns {null} - No renderiza nada visible
 */
const KeyboardNavigation = () => {
  const navigate = useNavigate();
  const { settings } = useAccessibility();
  
  useEffect(() => {
    // Solo habilitar la navegacin por teclado si est activada en la configuracin
    if (!settings.keyboardNavigation) {
      return;
    }
    
    // Manejar atajos de teclado
    const handleKeyDown = (event) => {
      // Solo procesar si Alt est presionado
      if (!event.altKey) {
        return;
      }
      
      // Evitar conflictos con atajos del navegador
      if (event.ctrlKey || event.metaKey) {
        return;
      }
      
      let handled = true;
      
      switch (event.key) {
        case 'h': // Alt + H: Ir a la pgina de inicio
          navigate('/');
          break;
        case 't': // Alt + T: Ir a la pgina de tareas
          navigate('/tasks');
          break;
        case 'f': // Alt + F: Ir a la pgina de finanzas
          navigate('/finances');
          break;
        case 'i': // Alt + I: Ir a la pgina de inversiones
          navigate('/investments');
          break;
        case 'g': // Alt + G: Ir a la pgina de metas financieras
          navigate('/financial-goals');
          break;
        case 'c': // Alt + C: Ir a la pgina de calendario
          navigate('/calendar');
          break;
        case 'a': // Alt + A: Ir a la pgina de anlisis
          navigate('/analytics');
          break;
        case 'p': // Alt + P: Ir a la pgina de perfil
          navigate('/profile');
          break;
        case '/': // Alt + /: Mostrar ayuda de atajos de teclado
          showKeyboardHelp();
          break;
        default:
          handled = false;
          break;
      }
      
      // Si se manej el atajo, prevenir el comportamiento predeterminado
      if (handled) {
        event.preventDefault();
      }
    };
    
    // Mostrar ayuda de atajos de teclado
    const showKeyboardHelp = () => {
      const shortcuts = [
        { key: 'Alt + H', description: 'Ir a la pgina de inicio' },
        { key: 'Alt + T', description: 'Ir a la pgina de tareas' },
        { key: 'Alt + F', description: 'Ir a la pgina de finanzas' },
        { key: 'Alt + I', description: 'Ir a la pgina de inversiones' },
        { key: 'Alt + G', description: 'Ir a la pgina de metas financieras' },
        { key: 'Alt + C', description: 'Ir a la pgina de calendario' },
        { key: 'Alt + A', description: 'Ir a la pgina de anlisis' },
        { key: 'Alt + P', description: 'Ir a la pgina de perfil' },
        { key: 'Alt + /', description: 'Mostrar esta ayuda' },
      ];
      
      // Crear el contenido del modal
      const helpContent = shortcuts.map(shortcut => 
        `${shortcut.key}: ${shortcut.description}`
      ).join('\\n');
      
      // Mostrar el modal
      alert('Atajos de teclado disponibles:\\n\\n' + helpContent);
    };
    
    // Agregar el event listener
    document.addEventListener('keydown', handleKeyDown);
    
    // Limpiar el event listener al desmontar
    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [navigate, settings.keyboardNavigation]);
  
  // Este componente no renderiza nada visible
  return null;
};

export default KeyboardNavigation;
